<template>
  <div id="box">
    <h1>introduce</h1>
    <div id="content">
      <p>
        html/css(html5css3)javascript(es6)
        jqueryvuewebpackwebpackgit
      </p>
    </div>
    <div class="line">
      <div class="textCard">
        <div class="text"
             @mouseenter="toggle('idx1',80)"
             @mouseleave="toggle('idx1',0)">
          <div class="bar idx1"></div>
        </div>
        <div class="blank">html+css</div>
      </div>
      <div class="textCard">
        <div class="text"
             @mouseenter="toggle('idx2',70)"
             @mouseleave="toggle('idx2',0)">
          <div class="bar idx2"></div>
        </div>
        <div class="blank">javaScript</div>
      </div>
      <div class="textCard">
        <div class="text"
             @mouseenter="toggle('idx3',80)"
             @mouseleave="toggle('idx3',0)">
          <div class="bar idx3"></div>
        </div>
        <div class="blank">jQuery</div>
      </div>
    </div>
    <div class="line">
      <div class="textCard">
        <div class="text"
             @mouseenter="toggle('idx4',60)"
             @mouseleave="toggle('idx4',0)">
          <div class="bar idx4"></div>
        </div>
        <div class="blank">Vue</div>
      </div>
      <div class="textCard">
        <div class="text"
             @mouseenter="toggle('idx5',50)"
             @mouseleave="toggle('idx5',0)">
          <div class="bar idx5"></div>
        </div>
        <div class="blank">webpack</div>
      </div>
      <div class="textCard">
        <div class="text"
             @mouseenter="toggle('idx6',40)"
             @mouseleave="toggle('idx6',0)">
          <div class="bar idx6"></div>
        </div>
        <div class="blank">PHP</div>
      </div>
    </div>

  </div>

</template>

<style>
  @import '../assets/css/homePage.css';

</style>

<script>
  import '../assets/js/jquery.js';

  export default{
    data:function(){
      return{
        show:true
    }
    },
    methods:{
      toggle:function(dom,value){
        $('.'+dom).stop(true).animate({
          height:value
        },500);
      }
    }
  }

</script>



