<template>
  <div id="box">
    <h1>My Skill List</h1>
    <div id="skillbox">
      <div :class="[{skilllist:true},{toggleStyle1:index%2==0},{toggleStyle2:index%2!=0}]"
           v-for="(skill,index) in skillcons">
        <div :class="[{skillItem:true},skill.idx,]"
             @mouseenter = "move(skill.idx,70)"
             @mouseleave = "move(skill.idx,10)">

          <img src="../assets/jpg/mark.png">
          <div class="p1">
            <h2>{{skill.skillName}}</h2>
            <p>{{skill.des}}</p>
          </div>

        </div>
      </div>
    </div>
  </div>

</template>

<style>
  @import '../assets/css/skillPage.css';
</style>

<script>
  import '../assets/js/jquery.js'
  export default{
    data(){
      return{
        skillcons:[
          {
            skillName:'HTML/CSS',
            des:`HTMLCSS
            ;HTML5CSS3`,
            idx:'l1'
          },
          {
            skillName:'javaScript',
            des:`javaScriptDOM`,
            idx:'l2'
          },
          {
            skillName:'jQuery',
            des:`jQuery`,
            idx:'l3'
          },
          {skillName:
            'PHP',
            des:`ajaxphp`,
            idx:'l4'
          },
          {skillName:
            'Vue',
            des:`Vue`,
            idx:'l5'
          },
          {
            skillName: 'Webpack',
            des:`webpackgit`,
            idx:'l6'
          },

        ]
      }
    },
    methods:{
      toggle(){
        this.show = !this.show;
      },
      move(dom,value){
        $('.'+dom).stop(true).animate({
          bottom:value
        },500);
      }
    }

  }
</script>



