<template>
  <div id="box">
    <div :class="[{pic:true},{trans:uni.show},uni.bgpic]"
         @mouseenter="toggle(uni)"
         @mouseleave="toggle(uni)"
         v-for="(uni,index) in universities">
      <transition name="slide">
        <div class="uniDes" v-show="uni.show">
          <h2>{{uni.studyTime}}</h2>
          <p>
            &emsp;&emsp;{{uni.uniDes}}
          </p>
        </div>
      </transition>
    </div>
  </div>

</template>

<style scoped>
  #box{
    width:800px;
    height:1200px;
    margin:50px auto;
    background:#fafafa;
  }
  #box .pic{
    position:relative;

    height:300px;
    width:100%;
    margin:50px 0;
    /*background-size:100%;*/
    /*background-repeat:no-repeat;*/
    /*background-image:url("../assets/jpg/uni1.jpg");*/
  }
  #box .trans{
    opacity:0.75;
  }
  #box .uniDes{
    position:absolute;
    top:25px;
    left:200px;
    width:600px;
    height:100px;

  }
  #box .uniDes p{
    font-weight: 600;
  }

  .slide-enter-active,.slide-leave-active{
    transition:all 1s;
  }
  .slide-enter,.slide-leave-to{
    transform:translateX(-40px);
    opacity:0;
  }

  .bg1{
    background-image:url("../assets/jpg/uni1.jpg");
  }
  .bg2{
    background-image:url("../assets/jpg/uni2.jpg");
  }
  .bg3{
    background-image:url("../assets/jpg/uni3.jpg");
  }




</style>

<script>
  export default{
    data(){
      return{
        universities:[
          {
            studyTime:'2011-2015',
            uniDes:`19561958
            1962
            19651980
            1985200020012004
            2015`,
            show:false,
            bgpic:'bg1'
          },
          {
            studyTime:'2017-2018',
            uniDes:`Konan University1951
            `,
            show:false,
            bgpic:'bg2'
          },
          {
            studyTime:'2015-2018',
            uniDes:`Xiamen UniversityXMU1921
            211985`,
            show:false,
            bgpic:'bg3'
          }
        ]

      }
    },
    computed:{
      backPic(){
        return{
          backgroundImage:"url("+require('../assets/jpg/uni1.jpg')+")",
          background:yellow
        };
      }
    },
    methods:{
      toggle(obj){
        obj.show = !obj.show;
      }
    }

  }

</script>



