<template>
  <header>

    <ul id="nav">
      <router-link :to="link.direct" v-for="(link,index) in links">
        <li @click = "active(index)"
          :class="{active:num==index}">
          {{link.tag}}
        </li>
      </router-link>
    </ul>

  </header>

</template>

<style scoped>
  .active{
    background: #3A3A3A;
  }

  ul{
    width:100%;
    height:50px;
    background:black;
  }
  ul li{
    display:inline-block;
    margin:0 40px;
    color:white;
    height:50px;
    width:100px;
    line-height:50px;
    text-align:center;
  }
  ul li a{
    color:white;
    text-decoration:none;
    font-size:14px;
  }
</style>

<script>
  export default{
    data:function(){
      return{
        direct:'/skill',
        links:[
          {direct:'/home',tag:''},
          {direct:'/skill',tag:''},
          {direct:'/project',tag:''},
          {direct:'/educate',tag:''},
        ],
        num:''
      }
    },
    methods:{
      active:function(index){
        this.num=index;
      }
    }
  }


</script>



